@font-face {
  font-family: "PT Sans";
  font-style: normal;
  font-weight: 400;
  src: url("../fonts/ptsans-400.woff2") format("woff2");
  font-display: swap;
}
@font-face {
  font-family: "PT Sans";
  font-style: normal;
  font-weight: 700;
  src: url("../fonts/ptsans-700.woff2") format("woff2");
  font-display: swap;
}
img {
  max-width: 100%;
  height: auto;
}

* {
  outline: 1px solid tomato;
}

.page {
  height: 100%;
  font-size: 18px;
  font-family: "PT Sans Narrow", "Arial", sans-serif;
  line-height: 26px;
}

.page__body {
  min-height: 100%;
  margin: 0;
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
}

.button {
  padding: 15px 0 17px;
  color: #ffffff;
  background-color: #81B3D2;
  font-weight: 700;
  font-size: 21px;
  line-height: 26px;
  text-transform: uppercase;
  text-decoration: none;
}

.main-header {
  display: flex;
  flex-direction: column;
  outline: 1px solid #d71818;
}

.main-header__logo {
  position: absolute;
  left: calc(50% - 92.5px);
  height: 96px;
}

.main-nav {
  font-weight: 700;
  font-size: 14px;
  line-height: 21px;
  text-transform: uppercase;
}

.main-nav__list {
  display: none;
  margin: 0;
  padding: 0;
  list-style: none;
}
@media (min-width: 768px) {
  .main-nav__list {
    display: grid;
    grid-template-columns: repeat(5, 1fr);
  }
}

.main-nav__item {
  background-color: #81B3D2;
  color: #ffffff;
}
@media (min-width: 768px) {
  .main-nav__item {
    background-color: #ffffff;
  }
}

@media (min-width: 768px) {
  .main-nav__item:nth-child(4n-1) {
    grid-column: 4/5;
  }
}

@media (min-width: 768px) {
  .main-nav__toggle {
    display: none;
  }
}

.main-nav__link {
  display: block;
  padding: 18px 17px;
  text-align: center;
  text-decoration: none;
  color: #000000;
}

.page-welcome {
  padding: 78px 0 90px;
  background-color: #acdbf9;
  background-image: url(../../img/description-background.jpg);
  background-repeat: no-repeat;
  background-size: cover;
  text-align: center;
}

.page-welcome--short {
  padding: 103px 0 122px;
}

.page-description {
  text-transform: uppercase;
  text-align: center;
}

.page-description__title {
  font-weight: 700;
  font-size: 21px;
  line-height: 26px;
}

.page-description__text {
  font-weight: 400;
  font-size: 14px;
  line-height: 21px;
}

.advantage__container {
  color: #ffffff;
  background-color: #81B3D2;
}

.review-form__fieldset {
  border: none;
  outline: 1px solid #de2d2d;
}

.review-form__input {
  font-family: "PT Sans", "Arial", sans-serif;
  background-color: #F2F2F2;
  border: none;
  font-weight: 700;
  font-size: 18px;
  line-height: 26px;
}

.catalog-photos__list {
  margin: 0;
  padding: 0;
  list-style-type: none;
}
@media (min-width: 768px) {
  .catalog-photos__list {
    padding: 0 60px;
    display: grid;
    grid-template-columns: 1fr 1fr;
  }
}

.catalog-photos__item {
  padding: 24px 20px 25px;
}
@media (min-width: 768px) {
  .catalog-photos__item {
    padding: 0;
  }
}

@media (min-width: 768px) {
  .catalog-photos__item:first-child {
    grid-column: 1/-1;
  }
}

.catalog-video {
  padding: 20px;
  color: #000000;
  text-transform: uppercase;
  text-align: center;
}

.catalog-video__heading {
  margin: 0;
  padding-bottom: 36px;
  font-style: normal;
  font-weight: 700;
  font-size: 18px;
  line-height: 26px;
}

.catalog-video__description {
  margin: 0;
  font-style: normal;
  font-weight: 400;
  font-size: 14px;
  line-height: 21px;
  margin-bottom: 36px;
}
@media (min-width: 768px) {
  .catalog-video__description {
    margin-bottom: 66px;
  }
}

.page-footer__wrapper {
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 29px 20px 23px;
}
@media (min-width: 768px) {
  .page-footer__wrapper {
    padding: 36px 60px;
    flex-direction: row;
    justify-content: space-between;
  }
}
@media (min-width: 1200px) {
  .page-footer__wrapper {
    padding: 36px 120px;
  }
}

.page-footer__copyright {
  display: flex;
}

.contacts {
  font-weight: 700;
  font-size: 21px;
  line-height: 26px;
  text-transform: uppercase;
}

.contacts__link {
  color: #000000;
  text-decoration: none;
}

.social-list__item {
  background-color: #81B3D2;
  list-style-type: none;
}

.copyright__text {
  font-weight: 400;
  font-size: 14px;
  line-height: 21px;
  text-transform: uppercase;
}